import React,{useState,useContext,} from 'react'
import {Link,useHistory} from 'react-router-dom'
import {UserContext} from '../App'
import M from 'materialize-css'

const Login = () =>{
    const {state,dispatch} = useContext(UserContext)
    const history = useHistory()
    const [email,setEmail] = useState("")
    const [password,setPassword] = useState("")
    const PostData = ()=>{
        if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            M.toast({html: "Invalid Email Entered",classes:"#c62828 red darken-3"})
            return 
        }
        fetch("/signin",{
            method:"post",
            headers:{
                "Content-Type":"application/json"
            },
            body:JSON.stringify({
                email,
                password
            })
        }).then(res=>res.json())
        .then(data=>{
            console.log(data)
            if(data.error){
                M.toast({html: data.error,classes:"#c62828 red darken-3"})
            }
            else{
                localStorage.setItem("jwt",data.token)
                localStorage.setItem("user",JSON.stringify(data.user))
                dispatch({type:"USER",payload:data.user})
                M.toast({html: "SignedIn Successfully",classes:"#43a047 green darken-1"})
                history.push('/')
            }
        }).catch(err=>{
            console.log(err)
        })
    }
    return(
        <div className="mycard">
            <div className="card auth-card">
                <h2>Instagram</h2>
                <input 
                    type="text"
                    placeholder="Email"
                    value = {email}
                    onChange = {(e)=>setEmail(e.target.value)}
                />
                <input 
                    type="password"
                    placeholder="Password"
                    value = {password}
                    onChange = {(e)=>setPassword(e.target.value)}
                />
                <button className="btn signin waves-effect waves-light #64b5f6 blue darken-1"
                onClick = {()=>PostData()}
                >
                    SignIn
                </button>
                <div className="check-btn">
                    <Link to="/reset"><h6>Forgot password ?</h6></Link>
                    <Link to="/signup"><h6>Don't have an account? <span>Sign up</span></h6></Link>
                </div>
            </div>
        </div>
    )
}

export default Login;